.NavItemes {
  display: flex;
  align-items: center;
  height: 40px;
}

.NavItemes li a {
  height: 100%;
  width: 100%;
  margin-right: 20px;
  display: flex;
  align-items: flex-end;
  align-items: center;
  font-size: 1.7rem;
  color: #282828;
}
.NavItemes li a .Icon {
  margin-right: 5px;
}
.NavItemes li a .Icon > div > svg {
  width: 2rem;
  height: auto;
}

.NavItemes a:active,
.NavItemes a.active,
.NavItemes a:hover {
  color: var(--org-primary);
}

@media (max-width: 700px) {
  .NavItemes {
    flex-direction: column;
    background-color: white;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    padding-left: 0;
  }
